<template lang="">
    <div>
        <el-carousel indicator-position="outside">
            <el-carousel-item v-for="item in Images" :key="item">
                <el-image
                :src="item"
                fit="fill"></el-image>
            </el-carousel-item>
          </el-carousel>
    </div>
</template>
<script>
    import image1 from '../assets/homes/1.jpg';
    import image2 from '../assets/homes/2.jpg';
    import image3 from '../assets/homes/3.jpg';
    import image4 from '../assets/homes/4.jpg';
    import image5 from '../assets/homes/5.jpg';
    import image6 from '../assets/homes/6.jpg';
export default {
    name:"Index",
    data() {
        return {
            Images:[image1,image2,image3,image4,image5,image6]
        }
    },
}
</script>
<style lang="">
    
</style>


