import Vue from 'vue'
import Router from 'vue-router'
import Index  from '../components/Index'
import List   from '../components/user/List.vue'
Vue.use(Router)

export default new Router({
  routes: [
    // {path: '/',redirect: Index},
    {path: '/index',component: Index},
    {path: '/users',component: List},
  ]
})



